.containre {
    background:url('./so-white.png');
    background-repeat: no-repeat;
    background-size: cover;
    // font-family: 'Quantico', sans-serif;
    font-family: 'KG Take On The World', sans-serif;
    font-size: 1.01rem;
    margin-left: auto;
    margin-right: auto;
    padding-top: 80px;
    padding-bottom: 80px;
}
.header{
    align-items: center;
    justify-content: center;
    display: flex;
    padding:2%;
}
// .navbar{
//     background-color: #4d1a6c;
//     display: flex;
