//     flex: auto;
//     border: 2px solid white;
//     border-radius: 15px;
// }
.containre ul{
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: auto;
    gap: 2rem;
    color: black !important;
}
.rightprt #upload{
    color: black;
    padding: 1%;
    border: 1px solid;
    border-color: white;
    border-radius: 10px;
    background-color: white;
    overflow: hidden;
    font-size: large;
}
#ques{
    padding-top: 10px !important;
    color: black !important;
    font-weight: bold;
}
li{
    flex: auto;
}
