.inped {
    border-radius: 10px; 
    width: 95%;
    border: 2px solid black;
    background-color: white;
    color: black;      
    padding: 10px;
    // margin: 0 0 20px 0;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
}


.inped::placeholder{
    color: gray;
}

#ddown{
    border-radius: 10px; 
    // width: 95%;
    background-color: white;
    color: black;      
    padding: 10px;
    margin: 0 0 20px 0;
    margin-left: auto;
    margin-right: auto;
}

#imgip{
    border-radius: 10px; 
    width: 80% !important;
    background-color: white;
    color: black;      
    padding: 10px;
    // margin: 0 0 20px 0;
    margin-left: auto;
    margin-right: auto;
}
.dt {
    // margin-left: 10%;
    margin-bottom: 0% !important;
    width: 240px;
    height: 240px;
    background-color: white;
    border: 2px solid black;
    border-radius: 50%;
    display: flex;
    // margin-right: 90px;
    margin: 10%;
}
#fill{
    width: 100%;
    margin-right: 10%;
    margin-left: 10%;
}
.container4{
    display: flex;
    flex: auto;
    // margin: 0 500px 0 0;
}
#fill {
    font-size: 40px;
    margin-left: 0%;
    font-weight: bold;
    font-style: oblique;
    /* padding: 10px; */
}
/* .container3{
    margin-left: 600px;
} */
h2{
    color: black !important;
    font-size: 25px;
    /* margin-left: 600px; */
}
.leftprt {    
    // position: absolute;
    border: 2px solid black;
    border-radius: 25px;
    display: block;
    padding: 4%;
    background-color: white;    
    height: fit-content;
    width: 50%;
    margin-left: 0%;
}
.description {
    background-color: #3E065F;
    border: none;
    border-radius: 20px;
    margin-bottom: 20px;
}
.dt #ip{
    display: flex;
    width: 100%;
    border-radius: 50%;
    // padding: 1%;
}
.rightprt {
    // margin-left: 800px;
    display: flex;
    flex-flow: column;
    flex: auto;
    align-items: center;
}
#make {
    display: flex;
}
