//     border-radius: 10px;
//     margin: 10px 0 10px 0;
// }
.sbmit1 {
    display: block;
    color: black;
    background-color:white ;
    font-size: larger;
    width: fit-content;
    height: fit-content;
    margin-left: 80%;
    width: 130px !important;
}
#upimp{
    color: black !important;
    width: fit-content;
    background-color: white;
    margin-bottom: 2%;
}
.container2 {
    margin: 2%;
}
@media (max-width: 900px) {
    .containre {   
      flex-direction: column !important;
      padding-top: 10px !important;
    }
    .container2, .navbar, .container4 {   
      flex-direction: column !important;
    }
  }

@media (max-width: 1250px ) and (min-width: 900px) {
    .containere {   
      flex-direction:row !important;
    }
    .container2, .navbar, .container4 {   
      flex-direction: row !important;
    }
    .rightprt{
        display: flex;
        align-self: flex-start;
    }
  }
.rightprt #disclaimer{
    color: gray ;
    padding-bottom: 10px;
}
.disc{
    color: gray !important;
    display: inline;
}
.slt {
    width: 95%;
    border: 2px solid black !important;
    background-color: white !important;
    color: black !important;      
    padding: 10px;
    margin-bottom: 2%;
}

@media (max-width: 900px) {
    .leftprt {
        width: 90%;
        margin: auto;
    }
    .rightprt {
        width: 100%;
        margin: auto;
        padding-bottom: 60px;
    }
}
